#LandingPage{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 100%;
  padding: 0;
}

#LandingPage .row{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.snippetWrap, .regWrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /*flex-grow: 1;*/
}

#LandingPage .tile{
  -ms-flex-positive: 1;
      flex-grow: 1;
}








